/**
 * S3 Client for BG-Remover Service
 *
 * Handles image upload/download operations for input and output buckets.
 */

import {
  S3Client,
  PutObjectCommand,
  GetObjectCommand,
  HeadObjectCommand,
} from '@aws-sdk/client-s3';
import { getSignedUrl } from '@aws-sdk/s3-request-presigner';

// S3 client singleton
let s3Client: S3Client | null = null;

/**
 * Get or create S3 client
 */
export function getS3Client(region: string = 'eu-west-1'): S3Client {
  if (!s3Client) {
    s3Client = new S3Client({ region });
  }
  return s3Client;
}

/**
 * Upload processed image to S3
 */
export async function uploadProcessedImage(
  bucket: string,
  key: string,
  imageBuffer: Buffer,
  contentType: string = 'image/png',
  metadata: Record<string, string> = {}
): Promise<string> {
  const client = getS3Client();

  await client.send(
    new PutObjectCommand({
      Bucket: bucket,
      Key: key,
      Body: imageBuffer,
      ContentType: contentType,
      ServerSideEncryption: 'AES256',
      Metadata: {
        'processed-by': 'bg-remover',
        'processed-at': new Date().toISOString(),
        ...metadata,
      },
    })
  );

  // Return the S3 URL
  return `https://${bucket}.s3.eu-west-1.amazonaws.com/${key}`;
}

/**
 * Download image from S3
 */
export async function downloadImage(
  bucket: string,
  key: string
): Promise<{ buffer: Buffer; contentType: string }> {
  const client = getS3Client();

  const response = await client.send(
    new GetObjectCommand({
      Bucket: bucket,
      Key: key,
    })
  );

  if (!response.Body) {
    throw new Error('Empty response body from S3');
  }

  // Convert stream to buffer
  const chunks: Uint8Array[] = [];
  const stream = response.Body as AsyncIterable<Uint8Array>;
  for await (const chunk of stream) {
    chunks.push(chunk);
  }
  const buffer = Buffer.concat(chunks);

  return {
    buffer,
    contentType: response.ContentType || 'image/png',
  };
}

/**
 * Check if object exists in S3
 */
export async function objectExists(bucket: string, key: string): Promise<boolean> {
  const client = getS3Client();

  try {
    await client.send(
      new HeadObjectCommand({
        Bucket: bucket,
        Key: key,
      })
    );
    return true;
  } catch (error: any) {
    if (error.name === 'NotFound' || error.$metadata?.httpStatusCode === 404) {
      return false;
    }
    throw error;
  }
}

/**
 * Generate a pre-signed URL for uploading
 */
export async function generateUploadUrl(
  bucket: string,
  key: string,
  contentType: string = 'image/png',
  expiresIn: number = 3600
): Promise<string> {
  const client = getS3Client();

  const command = new PutObjectCommand({
    Bucket: bucket,
    Key: key,
    ContentType: contentType,
  });

  return getSignedUrl(client, command, { expiresIn });
}

/**
 * Generate a pre-signed URL for downloading
 */
export async function generateDownloadUrl(
  bucket: string,
  key: string,
  expiresIn: number = 3600
): Promise<string> {
  const client = getS3Client();

  const command = new GetObjectCommand({
    Bucket: bucket,
    Key: key,
  });

  return getSignedUrl(client, command, { expiresIn });
}

/**
 * Generate output key for processed image
 */
export function generateOutputKey(
  tenant: string,
  productId?: string,
  format: string = 'png'
): string {
  const timestamp = Date.now();
  const randomId = Math.random().toString(36).substring(2, 8);

  if (productId) {
    return `${tenant}/products/${productId}/processed-${timestamp}-${randomId}.${format}`;
  }

  return `${tenant}/processed/${timestamp}-${randomId}.${format}`;
}

/**
 * Reset client (useful for testing)
 */
export function resetClient(): void {
  s3Client = null;
}
